

































import Vue from 'vue'
import Page from '@/components/Page.vue'

export default Vue.extend({
  components: { Page },
  data() {
    return {
      custom: false,
      dialog: false,
    }
  },
  methods: {
    show(type: string) {
      if (type === 'custom') {
        this.custom = true
      } else {
        this.dialog = true
      }
    },
    close(type: string) {
      if (type === 'custom') {
        this.custom = false
      } else {
        this.dialog = false
      }
    },
  },
})
